/**
 * Wordpress Dependencies
 */
import { __ } from "@wordpress/i18n";
/**
 * External Dependencies
 */
import { z } from "zod";
/**
 * Common schema types
 */
export const trueFalseField = z.enum(["0", "1"]);
/**
 * Slot schema for schedule management
 */
const slotSchema = z.object({
    start: z.string(), // 12-hour format (e.g., "12:00 PM")
    end: z.string(), // 12-hour format (e.g., "1:00 PM")
});
/**
 * Day schedule schema for individual days
 */
const dayScheduleSchema = z.object({
    status: z.enum(["on", "off"]), // Whether the day is active
    slots: z.array(slotSchema), // Array of time slots
});
/**
 * Days of the week using our schedule management format
 */
const scheduleDays = ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"];
/**
 * Schedule schema matching our ScheduleManagement component format
 */
const scheduleSchema = z.object(Object.fromEntries(scheduleDays.map((day) => [day, dayScheduleSchema])));
// Export schedule-related schemas for reuse
export { slotSchema, dayScheduleSchema, scheduleSchema, scheduleDays };
export const numberField = z
    .number({
    message: __("Please enter a valid number.", "wpcafe"),
})
    .nonnegative(__("Please enter a non-negative number.", "wpcafe"));
// Color validation schema that accepts both 3-digit (#RGB) and 6-digit (#RRGGBB) hex colors
export const colorField = z
    .string()
    .regex(/^#([0-9A-Fa-f]{3}|[0-9A-Fa-f]{6})$/, {
    message: __("Invalid color format. Use #RGB or #RRGGBB.", "wpcafe"),
});
export const iconField = z.object({
    type: z.enum(["predefined", "custom"]),
    value: z.string(),
});
export const settingsSchema = z
    .object({
    restaurant_type: z.array(z.enum(["food_ordering", "reservation"])),
    restaurant_name: z.string(),
    restaurant_email: z
        .string()
        .email({ message: __("Invalid email address", "wpcafe") }),
    restaurant_phone: z
        .string()
        .min(6, {
        message: __("Contact number must be at least 6 digits.", "wpcafe"),
    })
        .max(15, {
        message: __("Contact number must not exceed 15 digits.", "wpcafe"),
    })
        .regex(/^[\d+\-\s()]+$/, {
        message: __("Invalid contact number format.", "wpcafe"),
    }),
    restaurant_schedule: scheduleSchema,
    slot_interval: numberField,
    // Pickup schedule override
    override_pickup_schedule: trueFalseField.optional(),
    pickup_schedule: scheduleSchema.optional(),
    pickup_slot_interval: numberField.optional(),
    // Delivery schedule override
    override_delivery_schedule: trueFalseField.optional(),
    delivery_schedule: scheduleSchema.optional(),
    delivery_slot_interval: numberField.optional(),
    pickup_minimum_order_amount: numberField,
    pickup_prepare_time: numberField,
    pickup_show_date_in_checkout_page: z.boolean(),
    pickup_show_time_in_checkout_page: z.boolean(),
    enable_pickup_message: z.boolean(),
    pickup_message: z.string(),
    delivery_minimum_order_amount: numberField,
    delivery_prepare_time: numberField,
    delivery_show_date_in_checkout_page: z.boolean(),
    delivery_show_time_in_checkout_page: z.boolean(),
    enable_delivery_message: z.boolean(),
    delivery_message: z.string(),
    tipping_calculation_method: z
        .enum(["fixed_amount", "percentage_amount"])
        .optional(),
    tip_options: z.array(numberField).optional(),
    enable_custom_tipping: z.boolean().optional(),
    custom_tipping_label: z.string().optional(),
    order_type: z.string(),
    enable_order_notification: z.string(),
    enable_order_tip: z.string(),
    table_layout_configuaration: z.string(),
    reservation_minimum_guest: numberField,
    reservation_maximum_guest: numberField,
    reservation_advanced: numberField,
    reservation_total_seat_capacity: numberField,
    reservation_booking_amount: numberField,
    reservation_hold_time_late_guest: numberField,
    reservation_status: z.string(),
    multiply_booking_amount_with_guests: z.boolean(),
    enable_reservation_pending_message: z.boolean(),
    reservation_pending_message: z.string(),
    enable_reservation_confirmed_message: z.boolean(),
    reservation_confirmed_message: z.string(),
    enable_reservation_empty_schedule_message: z.boolean(),
    reservation_empty_schedule_message: z.string(),
    reservation_form_button_text: z.string(),
    reservation_confirmation_button_text: z.string(),
    reservation_cancellation_button_text: z.string(),
    reservation_close_state: z.string(),
    reservation_business_hour_label: z.string(),
    enable_sound_notification: z.boolean(),
    custom_notification_sound: z.string(),
    repeated_sound_minute: z.number().min(0),
    primary_color: colorField,
    secondary_color: colorField,
    calendar_language: z.string(),
    require_location: z.boolean(),
    display_location_selector: z.enum([
        "all_pages",
        "specific_pages",
        "dont_show",
    ]),
    location_selector_pages: z.array(numberField),
    mini_cart_style: z.enum(["style-1", "style-2"]),
    mini_cart_icon: iconField,
    mini_cart_empty_button_link: z.string(),
    cart_icon: iconField,
    menu_popup_icon: iconField,
    enable_custom_holiday: z.boolean(),
    custom_holidays: z.array(z.string()), // Array of date strings
    override_product_variation_list_layout: z.boolean(),
    override_woocommerce_default_layout: z.boolean(),
    // Setup progress widget tracking
    setup_progress_widget_visited: z.boolean().optional(),
    // Integration configurations
    fluentcrm_webhook_url: z.string().optional(),
})
    .partial(); //all fields are optional
